html {
  box-sizing: content-box;
  margin: 0;
  padding: 0;
  font-size: 10px;
}
body {
  background-color: rgb(217, 219, 219);
}
h1,
p {
  font-family: Lato;
}
div {
  text-align: center;
  padding: 0.3em;
}

.app {
  background-color: rgb(217, 219, 219);
}

.app-head {
  display: flex;
  justify-content: center;
}

h1 {
  font-size: 2.5rem;
  text-align: center;
}

.search {
  font-size: 1.5rem;
  text-align: center;
}

.app-description > p {
  margin: 5px;
}

.div-description {
  font-size: 1.5rem;
}

.center {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.resultBoard {
  position: relative;
  background-color: white;
  margin: 15px;
  padding: 20px;
  border: 2px solid red;
  border-radius: 30px;
  max-height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 270px);
  justify-content: space-between;
}
.card {
  position:relative;
  background: repeating-conic-gradient(  from 3deg at 25% 25%,rgb(0, 89, 255) 0%,  rgb(96, 214, 235) 15%,  rgb(0, 89, 255) 33%);
  padding: 5px 5px 47px;
  margin: 10px 10px;
  width: 210px;
  border: 10px solid rgb(251, 255, 0);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}


.card:nth-child(3n+1) {
  background: repeating-conic-gradient(  from 3deg at 25% 25%,rgb(17, 124, 40) 0%, rgb(31, 250, 78)  20%,  rgb(17, 124, 40) 33%);

}
.card div {
  font-size: 1.2em;
  margin: 3px;
  /* width: 130px; */
  /* position: relative; */
}
.card div.img {
  min-height: 100px;
  background: linear-gradient(0deg, #afb1b1, white 50%, #afb1b1);
}
.card button {
  background-color: white;
  border: none;
}

.to-bottom {
  position: absolute;
  bottom: 2px;
  left:50%;
  transform: translateX(-50%);
}

.ability {
  position: relative;
  height: 25px;
}
.ability-name {
  padding: 0;
  margin: 0;
  display: inline-block;
  width: fit-content;
}
.ability-description {
  position: absolute;
  left: 50%;
  top: 100%;
  z-index: 1;
  background-color: white;
  border-radius: 5px;
  border: 1px solid rgb(156, 155, 155);
  width: 160px;
}
span {
  text-transform: uppercase;
  font-weight: bold;
}
.button-to-pokedex {
  /* position: absolute; */
  position: fixed;
  margin: 0;
  padding: 0;
  /* top: 15px; */
  left: 5px;; 
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background: linear-gradient(to bottom, red 45%, black 45% 50%, white 50%);
  position: fixed;
  z-index: 2;
}
.circle-inside-pokedex {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 10px;
  width: 10px;
  height: 10px;
  border: 2px solid black;
}
.pokedex {
  position: fixed;
  z-index: 2;
  background-color: rgb(217, 219, 219);
  left: 50vw;
  top: 0;
  width: 50vw;
  height: 100vh;
  display: block;
  text-align: center;
  overflow-y: scroll;
}

.pokedex-welcome {
  font-size: 1.5rem;
  font-weight: bold;
}
.pokedex .card {
  margin: 10px auto;
}
.pokedex_closebutton {
  background-color: white;
  margin: 0;
  padding: 0;
  border-radius: 6px;
  width: 40px;
  height: 40px;
  position: relative;
  left: calc(100% - 60px);
  right: 10px;
}

.description_closebutton{
  position: relative;
  transform: scale(0.7);
  left: calc(100% - 40px);
  top: -5px;
}

.pokedex_closebutton::after,
.pokedex_closebutton::before {
  background: black;
  display: block;
  top: 0;
  width: 4px;
  height: 40px;
  transform: rotate(45deg) scale(0.7);
  content: '';
  position: relative;
  left: calc(50% - 2px);
}
.pokedex_closebutton::after {
  transform: rotate(-45deg) scale(0.7);
  top: -100%;
}
@media (max-width: 560px) {
  .pokedex {
    left: 0;
    width: 100vw;
    display: block;
  }
  .button-to-pokedex {
    transform: scale(0.85);
  }
}
